import { Screen } from '@components/UI'
import { useCalendarEvents } from '@hooks'
import { DrawerParamList } from '@navigation/DrawerNavigator'
import { DrawerScreenProps } from '@react-navigation/drawer'
import { format } from 'date-fns'
import { FC, useState } from 'react'
import { CalendarList, Calendar } from 'react-native-calendars'

type Props = DrawerScreenProps<DrawerParamList, 'CalendarScreen'>

const CalendarScreen: FC<Props> = () => {
  // const { markedDates, selectedDate, setSelectedDate } = useCalendarEvents()
  const [selectedDate, setSelectedDate] = useState(new Date())
  const markedDates = { [`${format(selectedDate, 'yyyy-MM-dd')}`]: { selected: true } }
  console.timeEnd('onDayPress')
  // TODO: this shit takes forever to rerender on android
  return (
    <Screen scrollable>
      <Calendar
        horizontal
        pagingEnabled
        markingType="multi-dot"
        markedDates={markedDates}
        onDayPress={day => {
          console.time('onDayPress')
          setSelectedDate(new Date(day.dateString))
        }}
        // onVisibleMonthsChange={months => {
        //   console.log('now these months are visible', months)
        // }}
        // markedDates={{
        //   '2022-05-09': { selected: true },
        //   '2022-05-10': {
        //     dots: [
        //       { color: 'red' },
        //       { color: 'blue' },
        //       { color: 'purple' },
        //       { color: 'cyan' },
        //       { color: 'green' },
        //     ],
        //   },
        // }}
      />
    </Screen>
  )
}
export default CalendarScreen
